body {
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  background: #fff;
  border-radius: 8px;
  max-width: 960px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

h1, h2 {
  text-align: center;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: 20px 0;
}

th, td {
  text-align: center;
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f4f4f4;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
}

form {
  margin-top: 20px;
}

form label {
  margin-bottom: 8px;
  display: block;
}

form input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 10px;
  padding: 6px;
}

#isEnrolled {
  height: 20px;
}

.edit__btn {
  background-color: #007bff;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-bottom: 10px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.delete__btn {
  background-color: #007bff;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
}

button:hover {
  background-color: #224d7b;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button {
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop {
  background-color: #0006;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  background-color: #fff;
  border-radius: 20px;
  flex-direction: column;
  padding: 30px;
  display: flex;
  position: relative;
}

#change-student-form {
  flex-direction: column;
  display: flex;
}

.cross {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  font-size: 20px;
  display: flex;
  position: absolute;
  top: 18px;
  right: 18px;
}

.is-hidden {
  display: none;
}
